.pets-problems__list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
  }
}

.pets-problems__item {
  background-color: $background-first;
}

.pets-problems__title {
  font-size: 24px;
  line-height: 37px;
  font-weight: inherit;
  text-transform: uppercase;
}

.pets-problems__description {
  font-family: $secondary-font;
  font-size: 14px;
  line-height: 18px;
  color: $color-text-main;
}

.pets-problems__link {
  font-size: 16px;
  line-height: 16px;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
}
